import React from 'react';
import './Cards.css';

class Cards extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            'date': '',
            'currencyRate': {},
            'usd': '',
            'eur': ''
        }
        this.currency = ['USD', 'RUB', 'EUR'];
    }
    getRate = () => {
        fetch('https://api.exchangeratesapi.io/v1/latest?access_key=dd8ebfa8aa54d2f628da3a070f0644c5')
            .then(data => {
                return data.json();
            })
            .then(data => {
                this.setState({ date: data.date });
               let result = {};
                for (let i = 0; i < this.currency.length; i++) {
                    result[this.currency[i]] = data.rates[this.currency[i]];
                }
                this.setState({ currencyRate: result });
                this.calc();
            });
    }
    calc = () => {
        let eur = this.state.currencyRate.RUB;
        eur = eur.toFixed(2);
        this.setState({ eur: eur});
        let usd = this.state.currencyRate.USD;
        let sum = eur / usd;
        sum = sum.toFixed( 2 );
        this.setState({ usd:  sum });
    }
    componentDidMount() {
        this.getRate();
    }
    render() { 
        return (
            <>  
                <div className='date-h'>
                    {this.state.date}
                </div>
                <div className='wrapper'>
                    <div className='cards'>
                        <div className='text'>
                            <span className='usd'>USD</span> = {this.state.usd} RUB
                        </div>
                    </div>
                    <div className='cards'>
                        <div className='text'>
                            <span className='eur'>EUR</span> = {this.state.eur} RUB
                        </div>
                    </div>
                </div>
            </>    
        )
    }
}

export default Cards;